import { Formik, Form, Field } from "formik";
import React, {useEffect} from "react";
import { useDispatch, useSelector } from "react-redux";
import { AuthActionCreators } from "../../redux/authReducer";
import "./Greeting.css";

export const Greeting = () => {
  let greetingText = "";
  let pathOfDay = new Date().getHours();

  if (pathOfDay < 7) {
    greetingText = "–î–æ–±—Ä–æ–π –Ω–æ—á–∏";
  } else if (pathOfDay < 13) {
    greetingText = "–î–æ–±—Ä–æ–µ —É—Ç—Ä–æ";
  } else if (pathOfDay < 19) {
    greetingText = "–î–æ–±—Ä—ã–π –¥–µ–Ω—å";
  } else {
    greetingText = "–î–æ–±—Ä—ã–π –≤–µ—á–µ—Ä";
  }

  const dispatch = useDispatch();
  let isAuth = useSelector((state) => state.auth.isAuth)
  const username = useSelector((state) => state.auth.username);

  const loginHandler = data => {
    dispatch(AuthActionCreators.login(data.username));
    localStorage.setItem("username", data.username)
  }
  const logoutHandler = () => {
    dispatch(AuthActionCreators.logout())
  }

  useEffect(() => {
    if (localStorage.getItem("username")) {
      dispatch(AuthActionCreators.login(localStorage.getItem("username")))
    }
  }, [localStorage.getItem("username")])

  return (
    <div className="greeting-container">
      {isAuth ? (
        <span className="greeting">
          {greetingText}, {username}! 
          <button className="greetingBtn" onClick={logoutHandler}>üö™</button>
        </span>
      ) : (
        <Formik
          initialValues={{
            username: "",
          }}
          onSubmit={(values) => loginHandler(values)}
        >
          <Form>
            <Field
              id="username"
              name="username"
              className="name"
              placeholder="[–í–≤–µ–¥–∏—Ç–µ –∏–º—è]"
            />
            <button className="greetingBtn" type="submit">
              –í—Ö–æ–¥
            </button>
          </Form>
        </Formik>
      )}
    </div>
  );
};
